body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

.button {
  display: inline-block;
  padding: 5px;
  cursor: pointer;
  text-transform: uppercase; }

.chat {
  position: fixed;
  z-index: 99999;
  bottom: 20px;
  right: 20px; }
  .chat__window {
    width: 360px;
    height: 600px;
    overflow: hidden;
    background-color: #fff;
    border: 1px solid #d6d6d6;
    border-radius: 5px 5px 0 0;
    box-shadow: 0px 0px 16px 3px rgba(0, 0, 0, 0.2);
    font-family: Arial,Helvetica,sans-serif;
    color: #404041; }
  .chat__header {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: #ededed;
    height: 35px;
    position: relative;
    z-index: 999;
    cursor: move;
    padding: 0 .25em; }
    .chat__header:after {
      content: '';
      position: absolute;
      border: 10px solid transparent;
      border-top: 10px solid #eee;
      top: 35px;
      right: 50%;
      -webkit-transform: translateX(50%);
              transform: translateX(50%); }
    .chat__header .title {
      -webkit-flex-grow: 1;
              flex-grow: 1;
      position: relative;
      height: 100%; }
      .chat__header .title:before {
        content: '';
        position: absolute;
        width: 105px;
        height: 25px;
        top: 8px;
        left: 3px;
        background-image: url(data:image/png;base64,);
        background-position: 0 0;
        background-size: 100%;
        background-repeat: no-repeat; }
    .chat__header .buttonMinimaze,
    .chat__header .buttonClose {
      color: #5c5c5c;
      border: none;
      background-color: transparent;
      font-size: 1.5em;
      cursor: pointer;
      padding: .6em;
      position: relative; }
      .chat__header .buttonMinimaze:before,
      .chat__header .buttonClose:before {
        position: absolute; }
    .chat__header .buttonMinimaze:before {
      content: '_';
      top: -6px;
      right: 9px; }
    .chat__header .buttonClose:before {
      content: '+';
      top: 3px;
      right: 8px;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg); }
  .chat__body {
    height: 565px;
    position: relative;
    background-color: #fff; }

.buttonShowChat__wrapper {
  position: fixed;
  z-index: 99998;
  bottom: 20px;
  right: 20px;
  cursor: pointer; }
  .buttonShowChat__wrapper .buttonShowChat {
    width: 88px;
    height: 66px;
    color: #0d1c34;
    background: transparent;
    cursor: pointer;
    box-shadow: 2px 3px 6px 1px rgba(40, 40, 40, 0.36);
    background-image: url(data:image/png;base64,);
    background-position: center 0;
    background-size: 98px;
    background-repeat: no-repeat; }

